// app/camera/[cameraName]/page.tsx
import { useRouter } from 'next/router';

const CameraPage = () => {
  const { query } = useRouter();
  const { cameraName } = query; // Lấy tên camera từ URL

  return (
    <div>
      <h1>Chi tiết {cameraName}</h1>
      <p>Thông tin chi tiết về {cameraName} sẽ hiển thị ở đây.</p>
      {/* Bạn có thể thêm các thông tin chi tiết về camera tại đây */}
    </div>
  );
};

export default CameraPage;